import React, { Component } from 'react';

import Icon24Add from '@vkontakte/icons/dist/24/add';
import Icon56CheckCircleOutline from '@vkontakte/icons/dist/56/check_circle_outline';
import Icon56InfoOutline from '@vkontakte/icons/dist/56/info_outline';

import {
	Separator, Div, Button, Panel, PanelHeader,
	Tabs, TabsItem, Spinner, Placeholder, Checkbox, FormStatus, Avatar, SimpleCell
} from "@vkontakte/vkui";



class Home extends Component {


	render() {

		const props = this.props;

		const { setPState, state, getTasks, getExpTasks, getAvatarUrl } = props;

		const { tasks, expTasks, tab } = state;
		return (
			<Panel separator={false} id={props.id}>
				<PanelHeader separator={false}>Задачи</PanelHeader>
				<Tabs>
					<TabsItem
						onClick={ getTasks }
						selected={ tab === 'active' }
					>
						Активные
					</TabsItem>
					<TabsItem
						onClick={ getExpTasks }
						selected={ tab === 'expires' }
					>
						Завершенные
					</TabsItem>
				</Tabs>
				<Separator />
				<Div>

				<Div style={{ padding: 5}}>
        <FormStatus style={{
          fontWeight: 400,
          marginBottom: -5,
          color: 'bright_light' ? '#6d7885' : '#909499'
        }}>
          В связи с пандемией коронавируса хотим напомнить вам, что нужно часто мыть руки, избегать мест скопления людей и по возможности не выходить из дома. 
          <div>
          <Button style={{
          left: -1,
          padding: 0,
          marginBottom: -5,
          color: 'bright_light' ? '#3f8ae0' : '#71aaeb'
       }}
               mode='tertiary'
               href="https://vk.com/covid19"
       >Главное о COVID-19</Button>
          </div>
        </FormStatus>
      </Div>

<Div></Div>

					{
						tasks.length > 0 && tab === 'active' ? ( tasks.map((e, key) => (
								<div key={key} >

									{ key !== 0 && <Separator/> }
										<div style={{ display: 'flex'}}>
							
											<div
												className={`test ${props.state.scheme === 'bright_light' ? 'light' : 'dark'}`}
												onClick={() => props.openModal(key)}
											>
												
											</div>
										</div>
								</div>
							))
							

						) : tasks &&
							<Placeholder
								icon={<Icon56CheckCircleOutline />}
								action={
									<Button before={ <Icon24Add /> }
											size='l' mode='primary' onClick={() => {
												setPState({ activeView: 'add' });
											}}
									>
										Создать
									</Button>
								}

							>
								Активных задач нет
							</Placeholder>

					}
					{
						expTasks.length > 0 && tab === 'expires' ? ( expTasks.map((e, key) => (
								<div key={key} >

									{ key !== 0 && <Separator/> }

									<div style={{ display: 'flex'}}>
					
										<div
											className={`test ${props.state.scheme === 'bright_light' ? 'light' : 'dark'}`}
											onClick={() => props.openModal(key)}
										>
											<div className='test2'>
												{e.title.length < 30 ? e.title : `${e.title.split('').slice(0,30).join('')}...`}
											</div>
										</div>
									</div>

								</div>
							))

						) : expTasks && tab === 'expires' && (
							<>
							<Placeholder
								icon={<Icon56InfoOutline />}
								action={<Button size="l" onClick={ getTasks } >Перейти к активным</Button>}
							>
								Завершённых задач нет
							</Placeholder>

							</>
						)
					}
					
						
				
				</Div>
				{props.snackbar}
			</Panel>
		);
	}

}

export default Home;
